  --btn-xs: calc(var(--btn-sm) / var(--btn-size-factor));
  --btn-sm: calc(var(--btn-md) / var(--btn-size-factor));
  --btn-md: var(--base-font-size);
  --btn-lg: calc(var(--btn-md) * var(--btn-size-factor));
