  --shadow2-strength-multiplier: calc(var(--shadow1-strength-multiplier) * var(--shadow-scale));
  --shadow2-blur-multiplier: calc(var(--shadow1-blur-multiplier) * var(--shadow-scale));
  --shadow3: calc(var(--shadow2) * var(--shadow-scale));
  --shadow3-strength-multiplier: calc(var(--shadow2-strength-multiplier) * var(--shadow-scale));
  --shadow3-blur-multiplier: calc(var(--shadow2-blur-multiplier) * var(--shadow-scale));
  --shadow4: calc(var(--shadow3) * var(--shadow-scale));
  --shadow4-strength-multiplier: calc(var(--shadow3-strength-multiplier) * var(--shadow-scale));
  --shadow4-blur-multiplier: calc(var(--shadow3-blur-multiplier) * var(--shadow-scale));
  --shadow5: calc(var(--shadow4) * var(--shadow-scale));
  --shadow5-strength-multiplier: calc(var(--shadow4-strength-multiplier) * var(--shadow-scale));
